td{
  text-align: left;
}
.btnDelete{
  background-color: red;
  padding: 5px;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}
.btnLogOut{
  padding: 5px;
  color: black;
  border-radius: 5px;
  cursor: pointer;
}
.btnUpdate{
  background-color: yellow;
  padding: 5px;
  color: black;
  border-radius: 5px;
  cursor: pointer;
}